import React, { useState, useRef } from "react";
import { useFrame } from "@react-three/fiber";

export default function FallingObjects(props) {
  const { objectList, fallLimitHeight } = props;
  const [objectNum, setObjectNum] = useState(0);
  const clickCount = useRef(0);
  const speed = useRef([0, 0, 0]);
  const rotation = useRef([0, 0, 0]);
  const isFalling = useRef(false);

  // correcting land position according to the height of the object
  const offset = objectList[objectNum]['offset'];
  const groundHeight = objectList[objectNum]['objectHeight'];

  const handleClick = () => { clickCount.current += 1; }
 
  const objectRef = useRef();
  useFrame((state) => {
    if (objectRef.current) {
      const G = 1;
      const terminal_velocity = 100;
      if (!isFalling.current) { // set initial position
        objectRef.current.position.x = offset[0];
        objectRef.current.position.y = offset[1];
        objectRef.current.position.z = offset[2];
        isFalling.current = true;
      }
      
      if (clickCount.current === 1) { // when clicked, the object starts falling at a random initial speed.
        speed.current = [(Math.random()-0.5)*20, 20+Math.random()*10, (Math.random()-0.5)*20];
        rotation.current = [(Math.random()-0.5)/30, (Math.random()-0.5)/30, (Math.random()-0.5)/30];
        clickCount.current += 1;
      }

      // objects fall according to basic laws of physics until they hit the ground or clicked
      if (clickCount.current > 0 || clickCount.current === 0 && objectRef.current.position.y > groundHeight) {
        objectRef.current.position.x += speed.current[0];
        objectRef.current.position.y += speed.current[1];
        objectRef.current.position.z += speed.current[2];
        
        objectRef.current.rotation.x += rotation.current[0];
        objectRef.current.rotation.y += rotation.current[1];
        objectRef.current.rotation.z += rotation.current[2];
      }

      // get next object after previos object reaches fall limit height
      if (objectRef.current.position.y < fallLimitHeight) {
        isFalling.current = false;
        clickCount.current = 0;
        objectRef.current.position.x = offset[0];
        objectRef.current.position.y = offset[1];
        objectRef.current.position.z = offset[2];
        objectRef.current.rotation.x = 0;
        objectRef.current.rotation.y = 0;
        objectRef.current.rotation.z = 0;
        speed.current = [0, 0, 0];
        rotation.current = [0, 0, 0];
        if (objectNum < objectList.length-1) {
          setObjectNum(objectNum+1)
        } else {
          setObjectNum(0)
        }
      }
      speed.current[1] = Math.max(speed.current[1] - G, -terminal_velocity);
    }
  });

  return (
    <group
    ref={objectRef}
    onClick={handleClick}>
      {objectList[objectNum]['object']}
    </group>
  );
}